const timeNumbers = [1, 10, 30, 60, 120];

function onlyContains(str)
{
  if (str.length === 0)
  {
    return false;
  }
  for (let i = 0; i < str.length; i++)
  {
    if (str[i] !== '!')
    {
      return false;
    }
  }
  return true;
}

function calculateData(intel, choices, originalData)
{
    let minuteData = new Array(41760).fill(0);
    let hourData = new Array(696).fill(0);
    let dayData = new Array(29).fill(0);
    let minuteDataAll = new Array(10080).fill(0);
    let hourDataAll = new Array(168).fill(0);
    let dayDataAll = new Array(7).fill(0);
    let online = [[], [], [], [], []];

    let peopleMinute = new Array(41760).fill("");
    let peopleHour = new Array(696).fill("");
    let peopleDay = new Array(29).fill("");

    let peopleList = [];

    for (let i = 0; i < intel.length - 1; i++)
    {
      if (intel[i].deletion !== "n/a" && intel[i].deletion[intel[i].deletion.length - 1] !== '|')
      {
        peopleList.push(' ' + intel[i].user);
      }
      else
      {
        peopleList.push(intel[i].user);
      }
      
      for (let j = 0; j < 5; j++)
      {
        if (intel[i].month.substring(intel[i].month.length - timeNumbers[j], intel[i].month.length).includes('1'))
        {
          online[j].push([intel[i].user, intel[i].world]);
          break;
        }
      }
      online.push(intel[intel.length - 1].update*1000);
      if (!onlyContains(choices) && (choices === "" || (!choices.includes('!') && choices.includes('|' + intel[i].user + '|')) || (choices.includes('!') && !choices.includes('|' + intel[i].user + '|'))))
      {
        for (let j = 0; j < intel[i].month.length; j++)
        {
          if (intel[i].month[j] === '1')
          {
            minuteData[j]++;
            peopleMinute[j] += ('|' + intel[i].user + '|');
          }
        }
        for (let j = 0; j < 7; j++)
        {
          for (let k = 0; k < 24; k++)
          {
            for (let l = 0; l < 60; l++)
            {
              minuteDataAll[j * 1440 + k * 60 + l] += intel[i].all[j][k][l];
            }
          }
        }
      }
    }
    peopleList.sort(function (a, b) {return a.toLowerCase().localeCompare(b.toLowerCase());});

    for (let i = 0; i < minuteData.length; i++)
    {
      hourData[parseInt(i / 60)] += minuteData[i];
      peopleHour[parseInt(i / 60)] += peopleMinute[i];
    }
    
    for (let i = 0; i < hourData.length; i++)
    {
      dayData[parseInt(i / 24)] += hourData[i];
      peopleDay[parseInt(i / 24)] += peopleHour[i];
    }
    for (let i = 0; i < minuteDataAll.length; i++)
    {
      hourDataAll[parseInt(i / 60)] += minuteDataAll[i];
    }
    for (let i = 0; i < hourDataAll.length; i++)
    {
      dayDataAll[parseInt(i / 24)] += hourDataAll[i];
    }
    return [online, minuteData, hourData, dayData, minuteDataAll, hourDataAll, dayDataAll, intel, peopleMinute, 
            peopleHour, peopleDay, peopleList, originalData];
}

module.exports = {calculateData};