const setColors = [[0, 255, 0], [192, 255, 0], [255, 255, 0], [255, 215, 0], [255, 128, 0], [255, 0, 0]]

function dayDifference(dayNumber, currentTime)
{
  const currentDay = new Date(currentTime).getDay();
  if (currentDay - dayNumber >= 0)
  {
    return currentDay - dayNumber;
  }
  else
  {
    return currentDay - dayNumber + 7;
  }
}

function updateExtremes(value, extremes, index=0, choices=[0], time=new Date(), hour=true)
{
  if (hour || choices[0] !== 28 || index < time.getHours())
  {
    if (extremes[0] > value)
    {
        extremes[0] = value;
    }
    if (extremes[1] < value)
    {
        extremes[1] = value;
    }
  }
  
  return;
}

function getColors(array, extremes, future, choices=[0], time=new Date(), type=-1)
{
  if (type === 0)
  {
    for (let i = 0; i < 60; i++)
    {
      if (choices[0] === 28 && ((choices[1] > time.getHours()) || (choices[1] === time.getHours() && i > time.getMinutes())))
      {
        array[i].push([192, 192, 192]);
      }
      for (let j = 0; j < 6; j++)
      {
        if (array[i][0] === j || j === 5)
        {
          array[i].push(setColors[j]);
          break;
        }
      }
    }
    
  }
  else
  {
    for (let i = 0; i < array.length; i++)
    {
      let normalize = ((array[i][0] - extremes[0]) / (extremes[1] - extremes[0])) * 511;
      if (extremes[0] === extremes[1] || (extremes[0] === Number.MAX_SAFE_INTEGER && extremes[1] === 0))
      {
          normalize = 256;
      }
      if (array[i][0] === 0)
      {
          if (future === 0)
          {
            if ((type === 1 && choices[0] === 28 && i > time.getHours())
            || (type === 2 && choices[2] === time.getDay() && i > time.getHours())
            || (type === 3 && choices[2] === time.getDay() && (choices[3] > time.getHours() || (choices[3] === time.getHours() && i > time.getMinutes())))
            || (type === 4 && i > time.getHours())
            || (type === 5 && (choices[4] > time.getHours() || choices[4] === time.getHours() && i > time.getMinutes())))
            {
                array[i].push([192, 192, 192]);
            }
            else
            {
              array[i].push([0, 255, 0]);
            }
        }
          else
          {
              array[i].push([0, 255, 0]);
          }
      }
      else if (normalize < 256)
      {
          array[i].push([normalize, 255, 0]);
      }
      else
      {
          array[i].push([255, 511 - normalize, 0]);
      }
    }
  }
    
    return;
}

function countDuplicates(array)
{
  for (let i = 0; i < array.length; i++)
  {
    let nameArray = [];
    let countArray = [];
    let currentIndex = 0;
    while (true)
    {
      let nextBreak = array[i].indexOf('|', currentIndex + 1);
      if (nextBreak === -1)
      {
        break;
      }
      if (nameArray.includes(array[i].substring(currentIndex + 1, nextBreak)))
      {
        countArray[nameArray.indexOf(array[i].substring(currentIndex + 1, nextBreak))]++;
      }
      else
      {
        nameArray.push(array[i].substring(currentIndex + 1, nextBreak));
        countArray.push(1);
      }
      currentIndex = nextBreak + 1;
    }
    let nameObject = nameArray.map((name, index) => ({name: name, count: countArray[index]}));
    nameObject.sort((a, b) => b.count - a.count);
    let returnString = "";
    for (let i = 0; i < nameObject.length; i++)
    {
      if (nameObject[i].count > 1440)
      {
        returnString += '|' + nameObject[i].name + " (" + (nameObject[i].count/1440).toFixed(2) + " days)|";
      }
      else if (nameObject[i].count > 60)
      {
        returnString += '|' + nameObject[i].name + " (" + (nameObject[i].count/60).toFixed(2) + " hrs)|";
      }
      else if (nameObject[i].count === 60)
      {
        returnString += '|' + nameObject[i].name + " (1 hr)|";
      }
      else if (nameObject[i].count === 1)
      {
        returnString += '|' + nameObject[i].name + " (1 min)|";
      }
      else
      {
        returnString += '|' + nameObject[i].name + " (" + nameObject[i].count + " mins)|";
      }
    }
    array[i] = returnString;
  }
}

function calculateDisplay(intel, choices, writtenChoices)
  {
    let recordedTime = new Date(intel[7][intel[7].length - 1].update*1000)
    let dayDisplay = new Array(28);
    let peopleDisplayDay = new Array(28).fill("");
    let extremes = [Number.MAX_SAFE_INTEGER, 0];
    for (let i = 0; i < 28; i++)
    {
        dayDisplay[i] = [intel[3][i + 1]];
        peopleDisplayDay[i] = intel[10][i + 1];
        updateExtremes(intel[3][i + 1], extremes);
    }
    getColors(dayDisplay, extremes, 1);
    countDuplicates(peopleDisplayDay);

    extremes = [Number.MAX_SAFE_INTEGER, 0];
    let hourDisplay = new Array(24);
    let peopleDisplayHour = new Array(24).fill("");
    for (let i = 0; i < 24; i++)
    {
        hourDisplay[i] = [intel[2][choices[0] * 24 + i]];
        peopleDisplayHour[i] = intel[9][choices[0] * 24 + i];
        updateExtremes(intel[2][choices[0] * 24 + i], extremes, i, choices, recordedTime, false);
    }
    getColors(hourDisplay, extremes, 0, choices, recordedTime, 1);
    countDuplicates(peopleDisplayHour);

    extremes = [Number.MAX_SAFE_INTEGER, 0];
    let minuteDisplay = new Array(60).fill(0);
    let peopleDisplayMinute = new Array(60).fill("");
    for (let i = 0; i < 60; i++)
    {
      minuteDisplay[i] = [intel[1][choices[0] * 1440 + choices[1] * 60 + i]];
      peopleDisplayMinute[i] = intel[8][choices[0] * 1440 + choices[1] * 60 + i];
      updateExtremes(intel[1][choices[0] * 1440 + choices[1] * 60 + i], extremes);
    }
    getColors(minuteDisplay, extremes, 0, choices, recordedTime, 0);

    extremes = [Number.MAX_SAFE_INTEGER, 0];
    let dayDisplayAll = new Array(7).fill(0);
    for (let i = 0; i < 7; i++)
    {
      dayDisplayAll[i] = [intel[6][i]];
      updateExtremes(intel[6][i], extremes);
    }
    getColors(dayDisplayAll, extremes, 1);
    if (window.screen.width < 500)
    {
      for (let i = 0; i < 7; i++)
      {
        let stringNumber = (dayDisplayAll[i][0]).toString();
        let roundedNumber = stringNumber.substring(2, 3);
        if (stringNumber.substring(3, 4) > '4')
        {
          roundedNumber = (Number(roundedNumber) + 1).toString();
          if (roundedNumber === '10')
          {
            roundedNumber = '0';
            let tempNumber = (Number(stringNumber.substring(0, 2)) + 1).toString();
            stringNumber = tempNumber + stringNumber.substring(2);
          }
        }
        switch (stringNumber.length)
        {
          case 5:
            stringNumber = stringNumber.substring(0, 2) + '.' + roundedNumber + 'k';
            break;
          case 6:
            stringNumber = stringNumber.substring(0, 2) + roundedNumber + 'k';
            break;
          case 7:
            stringNumber = stringNumber.substring(0, 1) + '.' + stringNumber.substring(1, 2) + roundedNumber + 'm';
            break;
          case 8:
            stringNumber = stringNumber.substring(0, 2) + '.' + roundedNumber + 'm';
            break;
          case 9:
            stringNumber = stringNumber.substring(0, 2) + roundedNumber + 'm';
          default:
            break;
        }
        dayDisplayAll[i][0] = stringNumber;
      }
    }

    extremes = [Number.MAX_SAFE_INTEGER, 0];
    let hourDisplayAll = new Array(24).fill(0);
    let peopleDisplayHourAll = new Array(24).fill("");
    let peopleDisplayDayAll = new Array(7).fill("");
    if (writtenChoices[1] === "")
    {
      for (let i = 0; i < 24; i++)
      {
        hourDisplayAll[i] = [intel[5][choices[2] * 24 + i]];
        updateExtremes(intel[5][choices[2] * 24 + i], extremes);
      }
      getColors(hourDisplayAll, extremes, 1);
    }
    else
    {
      let start = parseInt(writtenChoices[1].substring(0, writtenChoices[1].indexOf('-'))) - 1;
      let end = parseInt(writtenChoices[1].substring(writtenChoices[1].indexOf('-') + 1)) - 1;
      let initialStart = dayDifference(choices[2], new Date(intel[7][intel[7].length - 1].update*1000)) * 24;
      if (initialStart === 0)
      {
        end++;
        start++;
      }
      let dayOfWeek = new Date(intel[7][intel[7].length - 1].update*1000).getDay();
      for (let i = start; i <= end; i++)
      {
        for (let j = 0; j < 24; j++)
        {
          hourDisplayAll[j] += intel[2][672 - initialStart - (i * 168) + j];
          peopleDisplayHourAll[j] += intel[9][672 - initialStart - (i * 168) + j];
        }
      }
      if (initialStart !== 0)
      {
        start++;
        end++;
      }
      for (let i = start; i <= end; i++)
      {
        for (let j = 0; j < 7; j++)
        {
          let dayIndex = j + dayOfWeek;
          if (dayIndex >= 7)
          {
            dayIndex -= 7;
          }
          if (start !== 0 || (start === 0 && j === 0))
          {
            peopleDisplayDayAll[dayIndex] += intel[10][28 - (i * 7) + j];
          }
        }
      }
      countDuplicates(peopleDisplayDayAll);
      countDuplicates(peopleDisplayHourAll);
      for (let i = 0; i < 24; i++)
      {
        hourDisplayAll[i] = [hourDisplayAll[i]];
        updateExtremes(hourDisplayAll[i][0], extremes);
      }
      getColors(hourDisplayAll, extremes, end, choices, recordedTime, 2);
    }
    
    extremes = [Number.MAX_SAFE_INTEGER, 0];
    let minuteDisplayAll = new Array(60).fill(0);
    let peopleDisplayMinuteAll = new Array(60).fill("");
    if (writtenChoices[1] === "")
    {
      for (let i = 0; i < 60; i++)
      {
        minuteDisplayAll[i] = [intel[4][choices[2] * 1440 + choices[3] * 60 + i]];
        updateExtremes(intel[4][choices[2] * 1440 + choices[3] * 60 + i], extremes);
      }
      getColors(minuteDisplayAll, extremes, 1);
    }
    else
    {
      let start = parseInt(writtenChoices[1].substring(0, writtenChoices[1].indexOf('-'))) - 1;
      let end = parseInt(writtenChoices[1].substring(writtenChoices[1].indexOf('-') + 1)) - 1;
      let initialStart = dayDifference(choices[2], new Date(intel[7][intel[7].length - 1].update*1000)) * 1440;
      if (initialStart === 0)
      {
        end++;
        start++;
      }
      for (let i = start; i <= end; i++)
      {
        for (let j = 0; j < 60; j++)
        {
          minuteDisplayAll[j] += intel[1][41760 - initialStart - (i * 10080) - ((24 - choices[3]) * 60) + j];
          peopleDisplayMinuteAll[j] += intel[8][41760 - initialStart - (i * 10080) - ((24 - choices[3]) * 60) + j];
        }
      }
      countDuplicates(peopleDisplayMinuteAll);
      for (let i = 0; i < 60; i++)
      {
        minuteDisplayAll[i] = [minuteDisplayAll[i]];
        updateExtremes(minuteDisplayAll[i][0], extremes);
      }
      getColors(minuteDisplayAll, extremes, end, choices, recordedTime, 3);
    }
    
    extremes = [Number.MAX_SAFE_INTEGER, 0];
    let hourDisplayRange = new Array(24).fill(0);
    let peopleDisplayHourRange = new Array(24).fill("");
    let peopleDisplayDayRange = [""];
    let start = parseInt(writtenChoices[2].substring(0, writtenChoices[2].indexOf('-')));
    let end = parseInt(writtenChoices[2].substring(writtenChoices[2].indexOf('-') + 1));

    if (writtenChoices[2] === "")
    {
      for (let i = 0; i < 168; i++)
      {
        hourDisplayRange[i % 24] += intel[5][i];
      }
      for (let i = 0; i < 24; i++)
      {
        hourDisplayRange[i] = [hourDisplayRange[i]];
        updateExtremes(hourDisplayRange[i][0], extremes);
      }
      getColors(hourDisplayRange, extremes, 1, choices, recordedTime, 4)
    }
    else
    {
      for (let i = start; i <= end; i++)
      {
          for (let j = 0; j < 24; j++)
          {
              hourDisplayRange[j] += intel[2][672 - (i * 24) + j];
              peopleDisplayHourRange[j] += intel[9][672 - (i * 24) + j];
              peopleDisplayDayRange[0] += intel[9][672 - (i * 24) + j];
          }
      }
      countDuplicates(peopleDisplayHourRange);
      countDuplicates(peopleDisplayDayRange);
      for (let i = 0; i < 24; i++)
      {
          hourDisplayRange[i] = [hourDisplayRange[i]];
          updateExtremes(hourDisplayRange[i][0], extremes);
      }
      getColors(hourDisplayRange, extremes, end, choices, recordedTime, 4)
    }
    

    extremes = [Number.MAX_SAFE_INTEGER, 0];
    let minuteDisplayRange = new Array(60).fill(0);
    let peopleDisplayMinuteRange = new Array(60).fill("");

    if (writtenChoices[2] === "")
    {
      for (let i = 0; i < 60; i++)
      {
        for (let j = 0; j < 7; j++)
        {
          minuteDisplayRange[i] += intel[4][choices[4] * 60 + 1440 * j + i]
        }
      }
      for (let i = 0; i < 60; i++)
      {
        minuteDisplayRange[i] = [minuteDisplayRange[i]];
        updateExtremes(minuteDisplayRange[i][0], extremes);
      }
      getColors(minuteDisplayRange, extremes, 1, choices, recordedTime, 5);
    }
    else
    {
      for (let i = start; i <= end; i++)
      {
          for (let j = 0; j < 60; j++)
          {
              minuteDisplayRange[j] += intel[1][41760 - (i * 1440) - ((24 - choices[4]) * 60) + j];
              peopleDisplayMinuteRange[j] += intel[8][41760 - (i * 1440) - ((24 - choices[4]) * 60) + j];
          }
      }
      countDuplicates(peopleDisplayMinuteRange);
      for (let i = 0; i < 60; i++)
      {
        minuteDisplayRange[i] = [minuteDisplayRange[i]];
        updateExtremes(minuteDisplayRange[i][0], extremes);
      }
      getColors(minuteDisplayRange, extremes, end, choices, recordedTime, 5);
    }
    return [dayDisplay, hourDisplay, minuteDisplay, dayDisplayAll, hourDisplayAll, minuteDisplayAll, hourDisplayRange, minuteDisplayRange, 
            peopleDisplayMinute, peopleDisplayHour, peopleDisplayDay, peopleDisplayHourAll, peopleDisplayMinuteAll, peopleDisplayHourRange, 
            peopleDisplayMinuteRange, peopleDisplayDayAll, peopleDisplayDayRange];
  }

  module.exports = {calculateDisplay};