const binary = [128, 64, 32, 16, 8, 4, 2, 1];

function onlyContains(str)
{
  if (str.length === 0)
  {
    return false;
  }
  for (let i = 0; i < str.length; i++)
  {
    if (str[i] !== '!')
    {
      return false;
    }
  }
  return true;
}

function stringToBool(characterString, currentTime) {
    let booleanArray = "";
    for (let i = 0; i < characterString.length; i++) {
        let num = characterString.charCodeAt(i);
        for (let j = 0; j < 8; j++)
        {
        if (num - binary[j] >= 0) {
            booleanArray += "1";
            num -= binary[j];
        }
        else
        {
            booleanArray += "0";
        }
        }
    }
    let localTime = new Date(currentTime);
    booleanArray = booleanArray.substring(0, 41761 + localTime.getUTCHours() * 60 + localTime.getUTCMinutes());
    return booleanArray.substring(booleanArray.length - 40321 - (localTime.getHours() * 60) - localTime.getMinutes());
}

function shuffleArray(array)
{
  const timeOffset = (new Date).getTimezoneOffset() / 60;
  for (let j = 1; j < 7; j++)
  {
    for (let k = 0; k < timeOffset; k++)
    {
      array[j - 1].push(array[j].shift());
    }
  }
  for (let j = 0; j < timeOffset; j++)
  {
    array[6].push(array[0].shift());
  }
}

function stringToArray(characterString)
{
  let returnArray = [];
  let currentIndex = 0;
  while (true)
  {
    let nextBreak = characterString.indexOf('|', currentIndex + 1);
    if (nextBreak === -1)
    {
      break;
    }
    returnArray.push(characterString.substring(currentIndex + 1, nextBreak));
    currentIndex = nextBreak + 1;
  }
  return returnArray;
}

function noDisplay(array)
{
  if (array.length === 0)
  {
    return "none";
  }
  return "grid";
}

function buttonFeatures(arrayElement, choices)
{
  let returnArray = [arrayElement, "normal", "transparent"];
  if (arrayElement[0] === ' ')
  {
    returnArray[0] = arrayElement.substring(1);
    returnArray[1] = "italic";
  }
  if (!onlyContains(choices) && (choices === "" || (!choices.includes('!') && choices.includes('|' + returnArray[0] + '|')) || (choices.includes('!') && !choices.includes('|' + returnArray[0] + '|'))))
  {
    returnArray[2] = "yellow";
  }
  return returnArray;
}

module.exports = {stringToBool, shuffleArray, stringToArray, noDisplay, buttonFeatures};